<script defer="true"  type="text/javascript">

export default {
  name: 'app',
  beforeMount() {
    this.api_list = []
    let temp_str = `[`
    const modulepath = this.$route.path.split('/')[1]
    const files = require.context("@/data", true, /\.json$/).keys();
    for (let i = 0; i < files.length; i++) {
      const jsonfile = files[i]
      if (jsonfile.indexOf("/" + modulepath + "/")) {
        const oneMenu = require("@/data/" + jsonfile.replace("./", ""))

        if (oneMenu.children && oneMenu.children.length > 0) {
          for (let j = 0; j < oneMenu.children.length; j++) {
            const data = {
              root: oneMenu.root,
              path: oneMenu.path,
              name: oneMenu.name,
              api: '',
              comment: '',
            }
            const child = oneMenu.children[j]
            data.path += "/" + child.path
            if (child.comment)
              data.comment = child.comment
            if (child.api)
              data.api = child.api
            if (child.name)
              data.name = child.name
            this.api_list.push(data)
            if (data.api && data.api.uri) {
              // const temp = {}
              // temp[hash(data.api.uri)] = "2"
              temp_str += `{"${data.api.uri}":"0"},`

              const host = "pre-open-api.yunzhonghui.vip"  //swap-v2  OPEN API v2版本
              let generateCode = require("@/data/code/auto_generate.js").default
              const aaa = generateCode(data.api, this.codeLang, this.defaultCodeArea, host)
              // const temp = JSON.parse(`{"${data.api.uri}": "${aaa}"}`)
              const temp = {}
              temp[hash(data.api.uri)] = "golang"
              this.codeLang_list.push(temp)              // this.api_code_list.push(temp)
            }

          }

        } else {
          const data = {
            root: oneMenu.root,
            path: oneMenu.path,
            name: oneMenu.name,
            api: '',
            comment: '',
          }
          if (oneMenu.comment)
            data.comment = oneMenu.comment
          if (oneMenu.api)
            data.api = oneMenu.api
          this.api_list.push(data)
          if (data.api && data.api.uri) {
            temp_str += `{"${data.api.uri}":"0"},`
            const host = "pre-open-api.yunzhonghui.vip"  //swap-v2  OPEN API v2版本
            let generateCode = require("@/data/code/auto_generate.js").default
            const aaa = generateCode(data.api, this.codeLang, this.defaultCodeArea, host)
            // const temp = JSON.parse(`{"${data.api.uri}": "${aaa}"}`)
            const temp = {}
            temp[hash(data.api.uri)] = "golang"
            this.codeLang_list.push(temp)
          }


        }


      }

    }
    temp_str = temp_str.substring(0, temp_str.length - 1)
    temp_str += "]"
    console.log(temp_str)
    console.log(temp_str)
    this.api_code_list = JSON.parse(temp_str)
    // for(let m = 0; m < this.api_code_list.length; m++) {
    //   this.api_code_list
    // }
    console.log(this.api_code_list)

  },
  watch: {
    '$route.path': function (to, from) {
      if (to != from)
        this.$scrollTo('#' + this.$route.path.replace(/\//g, '_'), 10, { easing: 'ease', cancelable: false })
      if (to.split('/')[1] != from.split('/')[1]) {
        alert(to.split('/')[1])
        // this.$scrollTo('#'+this.$route.path.replace(/\//g,'_'), 1000, { easing: 'ease', cancelable: false })

      }
    }
  },
  mounted() {
    this.$scrollTo('#' + this.$route.path.replace(/\//g, '_'), 10, { easing: 'ease', cancelable: false })
  },
  methods: {
    bbbbb() {
      console.log("888")
    },
    hash(input) {
      var I64BIT_TABLE = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789_-'.split('');
      var hash = 5381;
      var i = input.length - 1;

      if (typeof input == 'string') {
        for (; i > -1; i--)
          hash += (hash << 5) + input.charCodeAt(i);
      }
      else {
        for (; i > -1; i--)
          hash += (hash << 5) + input[i];
      }
      var value = hash & 0x7FFFFFFF;

      var retValue = '';
      do {
        retValue += I64BIT_TABLE[value & 0x3F];
      }
      while (value >>= 6);

      return "aaa" + retValue;
    },
    async codeChanged(api) {
      // const host = "api-swap-rest.bingbon.pro"  //  swap  OPEN API 旧版本
      const host = "pre-open-api.yunzhonghui.vip"  //swap-v2  OPEN API v2版本
      // if (this.last_codeLang == this.codeLang) {
      //   return
      // }
      this.last_codeLang = this.codeLang
      let generateCode = require("@/data/code/auto_generate.js").default
      this.api_code_list[this.hash(api.uri)] = generateCode(api, this.codeLang_list[this.hash(api.uri)], this.defaultCodeArea, host)
    }
  },
  data() {
    return {
      api_list: [],
      api_code_list: [],
      radio: 3,
      langOptions: ['golang', 'nodeJS', 'shell'],
      codeLang_list: [],
      last_codeLang: "golang",
      codeArea: `package main

import "fmt"

func main() {
   const LENGTH int = 10
   const WIDTH int = 5  
   var area int
   const a, b, c = 1, false, "str" //多重赋值

   area = LENGTH * WIDTH
   fmt.Printf("面积为 : %d", area)
   println()
   println(a, b, c)  
}`,
      defaultCodeArea: `package main

import "fmt"

func main() {
   const LENGTH int = 10
   const WIDTH int = 5  
   var area int
   const a, b, c = 1, false, "str" //多重赋值

   area = LENGTH * WIDTH
   fmt.Printf("面积为 : %d", area)
   println()
   println(a, b, c)  
}`,
      activities: [{
        content: '当前最新版本: 更新了....',
        timestamp: '2024-04-12',
        size: 'large',
        color: '#0bbd87'
      }, {
        content: '更改了..abc..',
        timestamp: '2023-04-03',
      }, {
        content: '更改了..123..',
        timestamp: '2022-04-03',
        size: 'large'
      }, {
        content: '更改了..lalala..',
        timestamp: '2021-04-03'
      }]
    }
  }
}
function hash(input) {
  var I64BIT_TABLE = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789_-'.split('');
  var hash = 5381;
  var i = input.length - 1;

  if (typeof input == 'string') {
    for (; i > -1; i--)
      hash += (hash << 5) + input.charCodeAt(i);
  }
  else {
    for (; i > -1; i--)
      hash += (hash << 5) + input[i];
  }
  var value = hash & 0x7FFFFFFF;

  var retValue = '';
  do {
    retValue += I64BIT_TABLE[value & 0x3F];
  }
  while (value >>= 6);

  return "aaa" + retValue;
}

</script>

